.content{
  border:2px solid green;
  width: 500px;
  height: 500px;
  margin: 0 auto;
}
input {
  display: none;
}
.radio-open{
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 20px;
  height: 100px;
  background-color: lightgrey;
  writing-mode: vertical-rl;
  color: black;
}
.modal-window{
  display: none;
  top:0;
  left:0;
  bottom: 0;
  right: 0;
  position: fixed;
  background-color: lightslategray;
  justify-content: center;
  align-items: center;
}
#open:checked ~ .modal-window {
  display: flex;
}
.modal-content{
  position: relative;
  padding: 50px 10px 10px 30px;
  background-color: white;
  width: 60px;
  height: 40px;
}
.radio-close{
  position: absolute;
  background-color: lightgrey;
  width: 10px;
  height: 10px;
  top: -20px;
  left:100px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.radio-close:hover:after{
  content: attr(data-tips);
  display: block;
  position: absolute;
  left: 110%;
  top:-15px;
  background-color: lightskyblue;
}